/* cloud除了订购之外 */
/* cloudList */
.cloud-list .header {
  padding: 0.26rem 0.3rem;
  display: flex;
  justify-content: space-between;
}
.cloud-list .header .t-tit {
  font-size: 0.18rem;
  color: #040E34;
  line-height: 1;
}
.cloud-list .header .r-search {
  display: flex;
}
.cloud-list .header .r-search .ant-select-selector,
.cloud-list .header .r-search .ant-input-affix-wrapper {
  width: 2.3rem;
  line-height: 0.4rem;
  height: 0.4rem;
  margin-left: 0.3rem;
  border-radius: 0.04rem;
  font-size: 0.13rem;
  color: #9696A3;
}
.cloud-list .header .r-search .ant-select-selection-placeholder,
.cloud-list .header .r-search .ant-input,
.cloud-list .header .r-search .ant-select-selection-item {
  line-height: 0.4rem;
  font-size: 0.13rem;
  color: #9696A3;
}
.cloud-list .header .r-search .ant-select-single:not(.ant-select-customize-input) .ant-select-selector .ant-select-selection-search-input {
  height: 100%;
}
.cloud-list .link {
  cursor: pointer;
}
.cloud-list .link:hover {
  text-decoration: underline;
}
.cloud-list .opt-icon {
  width: 0.3rem;
  height: 0.3rem;
  transition: all 0.3s;
  text-align: center;
  line-height: 0.3rem;
  position: relative;
  margin-bottom: 0;
}
.cloud-list .opt-icon:hover {
  background: #F0F0FD;
  border-radius: 0.04rem;
}
.cloud-list .opt-icon .anticon {
  font-size: 0.2rem;
  color: #5C5EE4;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.cloud-list .country {
  width: 0.26rem;
  height: 0.18rem;
  margin-right: 0.15rem;
}
.cloud-list .area {
  display: flex;
  align-items: center;
}
.cloud-list .cloud-tab .ant-table {
  font-size: 0.14rem;
  color: #040E34;
}
.cloud-list .cloud-tab .status-tag {
  line-height: 0.26rem;
  border-radius: 0.04rem;
  text-align: center;
  width: 0.56rem;
}
.cloud-list .cloud-tab .host-name {
  font-size: 0.12rem;
  color: #9696A3;
  margin-top: 0.04rem;
  font-weight: 400;
}
.cloud-list .cloud-tab .ant-table-thead th {
  background: #F9F9FE;
  padding-top: 0.14rem !important;
  padding-bottom: 0.13rem !important;
  line-height: 1;
  padding-left: 0.3rem;
}
.cloud-list .cloud-tab .ant-table-thead th::before {
  display: none;
}
.cloud-list .cloud-tab .ant-table-tbody td {
  padding-top: 0.14rem;
  padding-bottom: 0.14rem;
  padding-left: 0.3rem;
  line-height: 1;
  box-sizing: border-box;
}
.cloud-list .power {
  margin-right: 0.08rem;
}
.cloud-list .keyword .ant-input-group-addon {
  display: none;
}
.cloud-list .cloud-os {
  width: 0.26rem;
  height: 0.26rem;
}
.cloud-opt {
  font-size: 0.12rem;
  color: #5C5EE4;
  margin-bottom: 0;
}
/* cloud-detail */
.cloud-detail {
  flex-direction: column;
  /* cloud-log */
}
.cloud-detail .summary {
  width: 77.945619%;
  margin: 0 auto 0.2rem;
}
.cloud-detail .summary .cloud-card {
  width: 100%;
  padding: 0.26rem 0.3rem;
}
.cloud-detail .summary p {
  margin-bottom: 0;
  display: block;
}
.cloud-detail .summary .tit {
  font-size: 0.16rem;
  color: #040E34;
  font-weight: bold;
  line-height: 1;
  margin-bottom: 0.3rem;
}
.cloud-detail .summary .item {
  clear: both;
  font-size: 0.14rem;
  margin-bottom: 0.3rem;
}
.cloud-detail .summary .item .l-tit {
  float: left;
  color: #9696A3;
  padding-left: 0.2rem;
}
.cloud-detail .summary .item .r-text {
  padding-left: 1.65rem;
  color: #040E34;
}
.cloud-detail .manage {
  width: 77.945619%;
  margin: 0 auto 0.2rem;
}
.cloud-detail .manage .item {
  background: rgba(255, 255, 255, 0.9);
  box-shadow: 0 0 16px 0 rgba(137, 138, 228, 0.12);
  padding: 0.4rem 0.3rem 0.3rem 0.3rem;
  border-radius: 0.12rem;
  min-height: 1.86rem;
}
.cloud-detail .manage .item .tit {
  font-size: 0.16rem;
  color: #040E34;
  font-weight: 500;
  margin-bottom: 0.12rem;
  line-height: 1;
}
.cloud-detail .manage .item .des {
  font-size: 0.12rem;
  color: #9696A3;
}
.cloud-detail .manage .item .ant-select-selection-item {
  line-height: 0.38rem;
}
.cloud-detail .manage .item .ant-select,
.cloud-detail .manage .item .ant-select {
  display: block;
  width: 1.6rem !important;
  line-height: 0.4rem;
  height: 0.4rem;
  border-radius: 0.04rem;
}
.cloud-detail .manage .item .ant-select-selector {
  width: 1.6rem;
  height: 0.4rem;
  line-height: 0.4rem;
}
.cloud-detail .manage .text {
  padding-left: 0.9rem;
}
.cloud-detail .manage .icon {
  width: 0.68rem;
  height: 0.68rem;
  float: left;
}
.cloud-detail .manage .icon img {
  width: 100%;
  height: 100%;
}
.cloud-detail .manage p {
  margin-bottom: 0;
}
.cloud-detail .manage .c-btn {
  line-height: 0.32rem;
  padding: 0 0.12rem;
  border-radius: 0.04rem;
  background: #F0F0FD;
  font-size: 0.14rem;
  color: #5C5EE4;
  display: inline-block;
  margin-top: 0.16rem;
  width: auto;
  cursor: pointer;
  transition: all 0.3s;
}
.cloud-detail .manage .c-btn:hover {
  background: #5C5EE4;
  color: #fff;
}
.cloud-detail .cloud-card {
  border-radius: 8px;
  width: 77.945619%;
  background: rgba(255, 255, 255, 0.9);
  box-shadow: 0 0 16px 0 rgba(137, 138, 228, 0.12);
  margin: 0 auto 0.2rem;
}
.cloud-detail .cloud-card .tabs {
  border-bottom: 1px solid #D9DBE1;
  padding: 0 0.1rem;
  margin-bottom: 0.3rem;
}
.cloud-detail .cloud-card .tabs span {
  padding: 0.17rem 0;
  display: inline-block;
  margin: 0 0.2rem;
  cursor: pointer;
  position: relative;
  font-size: 0.16rem;
  color: #525666;
}
.cloud-detail .cloud-card .tabs span::after {
  content: '';
  width: 0;
  height: 2px;
  background: #5C5EE4;
  bottom: 0;
  left: 50%;
  position: absolute;
  transition: all 0.2s;
}
.cloud-detail .cloud-card .tabs span.active::after {
  width: 100%;
  left: 0;
  bottom: -1px;
}
.cloud-detail .cloud-card .t-info {
  padding: 0.35rem 0.3rem 0.22rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.cloud-detail .cloud-card .t-info p {
  margin-bottom: 0;
  font-size: 0.14rem;
  color: #525666;
  line-height: 1;
}
.cloud-detail .cloud-card .t-info .tit {
  font-size: 0.22rem;
  color: #040E34;
  margin-bottom: 0.17rem;
}
.cloud-detail .cloud-card .t-info .country_code {
  width: 0.26rem;
  height: 0.18rem;
  margin-right: 0.11rem;
  display: inline-block;
  vertical-align: middle;
}
.cloud-detail .cloud-card .t-info span {
  display: inline-block;
  vertical-align: middle;
}
.cloud-detail .cloud-card .r-info {
  display: flex;
  align-items: center;
}
.cloud-detail .cloud-card .r-info .os {
  margin-left: 0.2rem;
}
.cloud-detail .cloud-card .r-info #status {
  width: 0.2rem;
  height: 0.2rem;
  margin-right: 0.2rem;
  display: inline-block;
  position: relative;
  padding-right: 0.2rem;
}
.cloud-detail .cloud-card .r-info #status::after {
  content: '';
  position: absolute;
  width: 1px;
  height: 0.16rem;
  background: #EBEDF3;
  right: -0.2rem;
  top: 50%;
  margin-top: -0.08rem;
}
.cloud-detail .cloud-card .r-info .opt {
  position: relative;
  margin-left: 0.2rem;
}
.cloud-detail .cloud-card .r-info .opt img {
  width: 0.24rem;
  height: 0.24rem;
  margin-left: 0.24rem;
  cursor: pointer;
}
.cloud-detail .cloud-card .r-info .opt::before {
  content: '';
  position: absolute;
  width: 1px;
  height: 0.16rem;
  background: #EBEDF3;
  left: 0;
  top: 50%;
  margin-top: -0.08rem;
}
.cloud-detail .cloud-log {
  padding-top: 0;
}
.cloud-detail .cloud-log .com-card {
  padding-top: 0.26rem;
}
.cloud-detail .cloud-log .com-card .ant-table-thead th {
  background: rgba(242, 242, 253, 0.4);
  font-size: 0.13rem;
  color: #9696A3;
  line-height: 1;
  padding: 0.14rem 0 0.14rem 0.3rem;
}
.cloud-detail .cloud-log .com-card .ant-table-thead th::before {
  display: none;
}
.cloud-detail .cloud-log .com-card .ant-table-tbody td {
  font-size: 0.14rem;
  color: #040E34;
  font-weight: 400;
  line-height: 2;
}
@media screen and (max-width: 1600px) {
  .cloud-detail .cloud-card {
    width: 90%;
  }
  .cloud-detail .summary,
  .cloud-detail .manage {
    width: 90%;
  }
  .cloud-detail .manage .item .ant-select,
  .cloud-detail .manage .item .ant-select {
    width: 1.2rem !important;
  }
}
