.power-modal {
  width: auto !important;
}
.power-modal.password-modal .ant-modal-content {
  width: 6.4rem;
}
.power-modal .ant-modal-header {
  border-radius: 0.12rem 0.12rem 0 0;
  border-bottom: 0;
  padding: 0.4rem 0.3rem 0.32rem 0.3rem;
}
.power-modal .ant-modal-title {
  font-size: 0.18rem;
  color: #040E34;
  line-height: 1;
  font-weight: 400;
}
.power-modal .ant-modal-content {
  width: 4.8rem;
  border-radius: 0.12rem;
  margin: 0 auto;
}
.power-modal .ant-modal-body {
  padding: 0 0.3rem 0.32rem 0.3rem;
}
.power-modal .ant-modal-body .ant-form-item {
  margin-bottom: 0.3rem;
}
.power-modal .ant-form-item,
.power-modal .ant-input {
  height: auto;
  line-height: 0.4rem;
}
.power-modal .ant-input-disabled {
  border: none;
}
.power-modal .ant-input {
  border-radius: 0.04rem;
  font-size: 0.13rem;
  color: #9696A3;
  padding: 0 0.16rem;
}
.power-modal .ant-col label {
  min-width: 0.62rem;
  font-size: 0.14rem;
  color: #040E34;
}
.power-modal .tip {
  font-size: 0.13rem;
  color: #040E34;
  margin-bottom: 0;
  line-height: 1;
}
.power-modal .force-off {
  margin-top: 0.1rem;
}
.power-modal .force-off label {
  font-size: 0.13rem;
  color: #525666;
}
.power-modal .force-off .ant-form-item-control-input {
  line-height: 1;
  min-height: auto;
}
.power-modal .force-off .ant-checkbox,
.power-modal .force-off .ant-checkbox-inner {
  width: 0.16rem;
  height: 0.16rem;
  border-radius: 0.04rem;
}
.power-modal .ant-form-item-explain-error {
  font-size: 0.12rem;
  margin-top: 0.05rem;
  color: #F64E60;
}
.power-modal .opt-btn {
  text-align: center;
  margin-bottom: 0 !important;
  margin-top: 0.57rem;
}
.power-modal .opt-btn .ant-btn {
  padding: 0 0.16rem;
  border-radius: 0.04rem;
  font-size: 0.14rem;
  line-height: 0.4rem;
  height: 0.4rem;
  margin: 0 0.04rem;
  letter-spacing: -0.02rem;
}
.power-modal .opt-btn .ant-btn span {
  height: 0.4rem;
}
.power-modal .opt-btn .ant-btn.sure {
  background: #5C5EE4;
  color: #fff;
  border-color: #5C5EE4;
}
.power-modal .opt-btn .ant-btn.cancel {
  border-color: #D9DBE1;
  color: #525666;
}
.power-modal .opt-btn .ant-btn.cancel:hover {
  background: #5C5EE4;
  color: #fff;
}
.power-modal .small-tip {
  font-size: 0.12rem;
  color: #9696A3;
  margin-top: -0.25rem;
  margin-bottom: 0.35rem;
}
.power-modal .c-btn {
  line-height: 0.44rem;
  height: 0.44rem;
  padding: 0 0.24rem;
  border-radius: 0.04rem;
  background: #F0F0FD;
  font-size: 0.14rem;
  color: #5C5EE4;
  display: inline-block;
  width: auto;
  cursor: pointer;
  transition: all 0.3s;
}
.power-modal .c-btn:hover {
  background: #5C5EE4;
  color: #fff;
}
